import { formatDate, reformatDate } from '@/utils/chronos'
import { AccountStatus, Currencies, Routes, WebsiteInfo } from '@/utils/enums'

import { USER_CONTEXT } from '@/store/modules/database/user'
import { mapGetters, mapMutations, mapState } from 'vuex'

export default {
	data() {
		return {
			Routes,
			Currencies,
			WebsiteInfo,
			currencies: [],
		}
	},
	async created() {
		this.$_.forIn(Currencies, (value, key) => {
			this.currencies.push(value)
		})

		this.$store.commit('global/updateCurrentPanelPath', this.$route.path, {
			root: true,
		})

		const user = await this.getItemFromDB({
			context: USER_CONTEXT,
			id: this.userData?.id,
		})

		if (
			user.success &&
			user.data &&
			user.data.status == AccountStatus.DISABLED
		) {
			this.destroySession()

			this.$router.replace(Routes.LOGIN.PATH)
		}
	},
	computed: {
		...mapState({
			userData: (state) => state.session.userData,
		}),
		...mapGetters({
			isUserConnected: 'session/isUserConnected',
			getSettingValue: 'setting/getSettingValue',
		}),
	},
	methods: {
		formatDate,
		reformatDate,
		...mapMutations({
			destroySession: 'session/destroySession',
		}),
	},
}
