import { Routes } from '@/utils/enums'

import { mapActions, mapGetters, mapState } from 'vuex'

export default {
	created() {
		const pass =
			this.restrictedScopes.findIndex((scope) =>
				this.$route.name.includes(scope.value)
			) === -1

		if (!pass) this.$router.replace(Routes.LOGIN.PATH)
	},
	computed: {
		...mapState({
			userData: (state) => state.session.userData,
			accessList: (state) => state.access.accessList,
		}),
		...mapGetters({
			userScopes: 'access/getUserScopes',
			restrictedScopes: 'access/getUserRestrictedScopes',
			disabledModuleScopes: 'access/getDisabledModuleScopes',

			userPageScopes: 'access/getUserPageScopes',
			userActionScopes: 'access/getUserActionScopes',
			userSectionScopes: 'access/getUserSectionScopes',

			userPageScope: 'access/getUserPageScope',
			userActionScope: 'access/getUserActionScope',
			userSectionScope: 'access/getUserSectionScope',
		}),
	},
	methods: {
		...mapActions('access', ['getAllAccess']),
		getAccessName(id) {
			const access = this.accessList.find((a) => a.id == id)

			if (access) return access.name

			return ''
		},
	},
}
