import { ref } from 'vue';
import { AccountAuthStatus, AccountStatus, Routes } from '@/utils/enums';
import { genPassword, getNameInitials, getRandomInteger } from '@/utils/functions';
import AccessManager from '@/mixins/access-manager';
import MailingMixin from '@/mixins/mailing';
import NotificationMixin from '@/mixins/notification';
import SettingsMixin from '@/mixins/settings';
import ViewsBaseMixin from '@/mixins/views-base';
import { SUBSCRIPTION_CONTEXT } from '@/store/modules/database/subscription';
import sanitizer from 'string-sanitizer';
import useClipboard from 'vue-clipboard3';
import { mapActions } from 'vuex';
export default {
  name: 'Subscription',
  mixins: [ViewsBaseMixin, AccessManager, MailingMixin, SettingsMixin, NotificationMixin],
  data() {
    return {
      Routes,
      account: {
        data: [],
        name: '',
        email: '',
        phone: '',
        status: true,
        authStatus: true
      },
      phoneObject: null,
      phoneInputOptions: {
        autocomplete: true,
        showDialCode: false,
        placeholder: this.$t('enterPhoneNumber')
      },
      phoneDropdownOptions: {
        width: '400px',
        showFlags: true,
        showSearchBox: true,
        showDialCodeInList: true,
        showDialCodeInSelection: false
      },
      rules: {
        name: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      currentEditItem: null,
      sendLoginInfo: false,
      accountDialog: false,
      isLoading: false,
      tableData: [],
      pageSize: 6,
      search: '',
      total: 0,
      page: 1
    };
  },
  computed: {
    pagedTableData() {
      this.total = this.searching.length;
      return this.searching.slice(this.pageSize * this.page - this.pageSize, this.pageSize * this.page);
    },
    searching() {
      if (!this.search) {
        this.total = this.tableData.length;
        return this.tableData;
      }
      this.page = 1;
      return this.tableData.filter(data => data.name.toLowerCase().includes(this.search.toLowerCase()));
    },
    isEmailValid() {
      const valid = this.$validator.isEmail(this.account.email);
      if (!valid) this.sendLoginInfo = false;
      return valid;
    }
  },
  created() {
    this.init();
  },
  methods: {
    getNameInitials,
    ...mapActions('subscription', ['checkAccount', 'getAllAccounts']),
    setPage(val) {
      this.page = val;
    },
    indexMethod(index) {
      return index + (this.page - 1) * this.pageSize + 1;
    },
    phoneInput(number, object) {
      this.phoneObject = object;
    },
    async init() {
      this.isLoading = true;
      const resp = await this.getAllAccounts();
      if (resp.data) this.tableData = resp.data;
      if (resp.failure) this.$message.error(this.$t('unableToUpdateData'));
      this.isLoading = false;
    },
    async genUid(uidSrc, addSalt) {
      var uid = sanitizer.sanitize(uidSrc.toLowerCase()).substring(0, 10);
      if (addSalt) uid += this.$_.random(1, 99, false);
      var task = await this.checkAccount(uid);
      if (task.success && !task.data) {
        return uid;
      } else {
        return await this.genUid(uidSrc, true);
      }
    },
    addItem() {
      this.currentEditItem = null;
      this.account.data = [];
      this.account.name = '';
      this.account.email = '';
      this.account.phone = '';
      this.account.status = true;
      this.account.authStatus = true;
      this.accountDialog = true;
    },
    async editItem(index, item) {
      this.currentEditItem = item;
      this.account.name = item.name;
      this.account.email = item.email;
      this.account.phone = item.phone;
      this.account.status = !!parseInt(item.status);
      this.account.authStatus = !!parseInt(item.auth_status);
      this.account.data = JSON.parse(item.data);
      this.accountDialog = true;
    },
    deleteItem(index, item) {
      this.$confirm(this.$t('deleteAccountQuestion'), this.$t('delete'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const task = await this.deleteItemInDB({
          context: SUBSCRIPTION_CONTEXT,
          id: item.id
        });
        if (task.success) {
          this.$message.success(this.$t('success'));
          this.init();
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }).catch(() => {
        /* empty */
      });
    },
    submitForm(ref) {
      this.$refs[ref].validate(async valid => {
        if (valid) {
          if (this.phoneObject.formatted && !this.phoneObject.valid) {
            this.$message.warning(this.$t('invalidNumber'));
            return false;
          }
          if (this.account.email && !this.isEmailValid) {
            this.$message.warning(this.$t('invalidEmail'));
            return false;
          }
          let credentials = {};
          if (ref === 'addForm') {
            const uid = await this.genUid(this.account.name, false);
            credentials = {
              uid: uid,
              name: this.account.name,
              email: this.account.email,
              phone: this.phoneObject.number || '',
              status: this.account.status ? AccountStatus.ENABLED : AccountStatus.DISABLED,
              auth_status: this.account.authStatus ? AccountAuthStatus.COMPLETED : AccountAuthStatus.NOT_COMPLETED,
              auth_code: getRandomInteger(1000, 9999),
              hash_id: this.$uuidv4(),
              password: genPassword(),
              data: JSON.stringify(this.account.data)
            };
            const task = await this.addItemInDB({
              context: SUBSCRIPTION_CONTEXT,
              data: credentials
            });
            if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
          } else {
            const newData = {};
            credentials = this.currentEditItem;
            if (this.currentEditItem.name !== this.account.name) {
              newData['name'] = this.account.name;
            }
            if (this.currentEditItem.email !== this.account.email) {
              newData['email'] = this.account.email;
            }
            if (this.phoneObject.number) {
              if (this.phoneObject.number !== this.currentEditItem.phone) newData['phone'] = this.phoneObject.number;
            } else {
              newData['phone'] = '';
            }
            if (!!parseInt(this.currentEditItem.status) !== this.account.status) {
              newData['status'] = this.account.status ? AccountStatus.ENABLED : AccountStatus.DISABLED;
            }
            if (!!parseInt(this.currentEditItem.auth_status) !== this.account.authStatus) {
              newData['auth_status'] = this.account.authStatus ? AccountAuthStatus.COMPLETED : AccountAuthStatus.NOT_COMPLETED;
            }
            if (this.currentEditItem.data !== this.account.data) {
              newData['data'] = JSON.stringify(this.account.data);
            }
            if (!this.$_.isEmpty(newData)) {
              const task = await this.updateItemInDB({
                context: SUBSCRIPTION_CONTEXT,
                id: this.currentEditItem.id,
                data: newData
              });
              if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
            }
          }
          if (this.sendLoginInfo) {
            this.sendEmail(null, `${this.$t('loginInfo')} - ${this.tr(this.websiteInfo.title)} - ${process.env.VUE_APP_NAME}`, `${this.$t('loginInfo')} - ${this.tr(this.websiteInfo.title)} - ${process.env.VUE_APP_NAME}`, `<small><b>${this.$t('id')}</b> : ${credentials.uid} <br> <b>${this.$t('password')}</b> : ${credentials.password}</small>`, credentials.email);
          }
          this.cancelForm(ref);
          this.init();
        } else {
          return false;
        }
      });
    },
    cancelForm(ref) {
      this.$refs[ref].resetFields();
      this.accountDialog = false;
    },
    viewPassword(password) {
      this.$alert(password, {
        center: true,
        showClose: true,
        closeOnClickModal: true,
        showCancelButton: false,
        showConfirmButton: false,
        closeOnPressEscape: true,
        icon: 'lock'
      });
    },
    async copyPassword(password) {
      const {
        toClipboard
      } = useClipboard();
      try {
        await toClipboard(password);
        this.$message.success(this.$t('success'));
      } catch (e) {
        console.error(e);
        this.$message.error(this.$t('anErrorOccured'));
      }
    }
  }
};