import "core-js/modules/es.array.push.js";
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    list: {
      type: Array,
      default: () => []
    },
    namePlaceholder: {
      type: String,
      default: null
    },
    valuePlaceholder: {
      type: String,
      default: null
    },
    nameReadonly: {
      type: Boolean,
      default: false
    },
    valueReadonly: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    addBtnDisabled: {
      type: Boolean,
      default: false
    },
    deleteBtnDisabled: {
      type: Boolean,
      default: false
    }
  },
  emits: ['onFeatureAdd', 'onFeatureRemove'],
  methods: {
    addFeature() {
      this.$emit('onFeatureAdd');

      // eslint-disable-next-line vue/no-mutating-props
      this.list.push({
        name: '',
        value: ''
      });
    },
    removeFeature(index) {
      this.$emit('onFeatureRemove', index, this.list[index]);

      // eslint-disable-next-line vue/no-mutating-props
      this.list.splice(index, 1);
    }
  }
};